'use client';

import { motion } from 'framer-motion';
import '@/components/Hero.css';
import { HeroProps } from '@/interfaces/hero';
import { useRef, useEffect } from 'react';

const banner = {
  animate: {
    transition: {
      delayChildren: 0.4,
      staggerChildren: 0.1,
    },
  },
} as const;

const letterAnimation = {
  initial: { y: 400 },
  animate: {
    y: 0,
    transition: {
      ease: [0.6, 0.01, -0.05, 0.95],
      duration: 1,
    },
  },
};

export const Hero = ({ homeInfo }: HeroProps) => {
  const { encabezado_1, encabezado_2, encabezado_3, descripcion, video } =
    homeInfo;
  const videoRef = useRef<HTMLVideoElement>(null);

  useEffect(() => {
    if (videoRef.current) {
      videoRef.current.volume = 0.1; // volumen entre 0.0 (mute) y 1.0 (m√°ximo)
    }
  }, []);

  return (
    <section className="p-inline relative mx-auto flex max-w-[1440px] flex-col justify-center">
      <div className="banner mx-auto flex w-full max-w-[1400px] grow flex-col">
        <BannerRow title={encabezado_1} />
        <BannerRow
          title={encabezado_2}
          delay={1}
          className="selft-start sm:self-center"
        />
        <BannerRow
          title={encabezado_3}
          delay={2}
          className="self-start sm:self-end"
        />
      </div>
      <motion.p
        initial={{ opacity: 0, y: 40 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{
          ease: 'easeInOut',
          duration: 1,
          delay: 1.2,
        }}
        className="float-text max-w-[450px] self-center lg:absolute lg:top-14 lg:mt-4"
      >
        {descripcion}
      </motion.p>

      <motion.div
        initial={{ opacity: 0, y: 80 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{
          ease: [0.76, 0, 0.24, 1],
          duration: 1,
          delay: 1.4,
        }}
        className="aspect-video w-full max-w-[840px] self-center"
      >
        <video
          ref={videoRef}
          controls
          preload="metadata"
          className="aspect-video h-auto w-full max-w-4xl rounded-lg shadow-2xl"
        >
          <source src={video} type="video/mp4" />
        </video>
      </motion.div>
    </section>
  );
};

const BannerRow = ({
  title,
  className,
  delay,
}: {
  title: string;
  className?: string;
  delay?: number;
}) => {
  return (
    <div className={`banner-row ${className ? `${className}` : ''}`}>
      <AnimatedLetters title={title} delayNumber={delay} />
    </div>
  );
};

const AnimatedLetters = ({
  title,
  disabled,
  delayNumber,
}: {
  title: string;
  disabled?: boolean;
  delayNumber?: number;
}) => {
  return (
    <motion.h1
      className="row-title"
      variants={disabled ? undefined : banner}
      initial="initial"
      animate="animate"
      transition={{ duration: 1, delay: delayNumber }}
    >
      {[...title].map((letter, key) => (
        <motion.span
          key={key}
          className="row-letter"
          variants={disabled ? undefined : letterAnimation}
        >
          {letter}
        </motion.span>
      ))}
    </motion.h1>
  );
};
